<template>
  <div class="finsh">
    <div :class="`finsh-container ${tData.finsh.container.avatar.img.url?`have-avatar`:''}`" :style="`background: url(${tData.finsh.container.bgurl})  0% 0% / 100% 100% no-repeat;${tData.finsh.container.style}`">
      <div class="finsh-avatar" :style="`${tData.finsh.container.avatar.style}`" v-if="tData.finsh.container.avatar.img.url">
        <div class="avatar-container" :style="`${tData.finsh.container.avatar.img.containerStyle}`" ref="avatar-container">
          <!-- <img v-if="!userInfo.nickname" :src="tData.finsh.container.avatar.img.url" :style="`${tData.finsh.container.avatar.img.style}`"> -->
        </div>
        <p class="avatar-name" :style="`${tData.finsh.container.avatar.name.style}`">{{userInfo.nickname || tData.finsh.container.avatar.name.text}}</p>
      </div>
      <div class="finsh-result" id="finshHead">
        <span style="padding-left: .5rem;" :style="tData.finsh.container.result.txt.style">{{tData.finsh.container.result.txt.text[0] || '答题总用时：'}}</span>
        <span class="finsh-time"  :style="tData.finsh.container.result.timeStyle">{{timeCost}}s</span>
        <span class="finsh-score" :style="tData.finsh.container.result.txt.style">{{tData.finsh.container.result.txt.text[1] || '本次挑战获得'}}</span>
        <div class="finsh-fraction" :style="tData.finsh.container.result.score.containerStyle">
          <span :style="`background: url(${tData.finsh.container.result.score.bgurl})  0% 0% / 100% 100% no-repeat;${tData.finsh.container.result.score.style}`">{{score}}</span>
        </div>
        <span class="finsh-score" :style="tData.finsh.container.result.txt.style">{{tData.finsh.container.result.txt.text[2] || '分'}}</span>
      </div>
      <div class="finsh-brand" :style="`background:url(${tData.finsh.container.brand.bgurl})  0% 0% / 100% 100% no-repeat;${tData.finsh.container.brand.style}`">
        <p :style="`${tData.finsh.container.brand.textStyle}`">{{tData.finsh.container.brand.text||'你的冷知识库等级为'}}</p>
        <div class="finsh-grade"  :style="`${tData.finsh.container.brand.img.containerStyle}`">
          <img v-for="(v, i) in remark.titleMatching" :key="i" :src="v" :style="`${tData.finsh.container.brand.img.style}`">
        </div>
      </div>
      <div class="finsh-banner"  :style="tData.finsh.container.remark.img.containerStyle" >
        <img  :style="tData.finsh.container.remark.img.style" :src="remark.matching">
      </div>
      <p class="finsh-explan" :style="tData.finsh.container.remark.textStyle" v-for="(v, i) in remark.text.split('&')" :key="i">{{v}}</p>
      <p class="finsh-tip" :style="tData.finsh.container.tip.style" id="finshTip">{{tData.finsh.container.tip.text || '长按保存截图'}}</p>
    </div>
    <div class="finsh-btn" id="finshBtn">
      <button v-for="(v, i) in tData.finsh.btn" :key="i" @click="domain(v.event)" :style="`background: url(${v.bgurl})  0% 0% / 100% 100% no-repeat;${v.style}`">{{v.text}}</button>
    </div>
    <div class="spare-parts"  v-show="false">
      <!-- 截图专用头部 -->
      <div id="headshot" class="finsh-result finsh-screenshot">
        <clock :done="false"></clock>
        <div class="finsh-headshot">
          <span class="finsh-score" :style="tData.finsh.container.result.txt.style">{{tData.finsh.container.result.txt.text[1] || '本次挑战获得'}}</span>
          <div class="finsh-fraction" :style="tData.finsh.container.result.score.containerStyle">
            <span :style="`background: url(${tData.finsh.container.result.score.bgurl})  0% 0% / 100% 100% no-repeat;${tData.finsh.container.result.score.style}`">{{score}}</span>
          </div>
          <span class="finsh-score" :style="tData.finsh.container.result.txt.style">{{tData.finsh.container.result.txt.text[2] || '分'}}</span>
        </div>
      </div>
      <!-- 截图专用尾部 -->
      <div id="footshot" class="finsh-footshot">
        <img :src="tData.finsh.qrcode.img.url" :style="tData.finsh.qrcode.img.style">
        <p>{{tData.finsh.qrcode.txt.text||'这么冷的知识库还有谁？'}}</p>
      </div>
    </div>
    <div id="screenshot" ></div>
  </div>
</template>

<script>
import * as html2canvas from 'html2canvas';
import * as canvas2image from 'canvas2image';
import clock from '../components/clock';
import Share from '../util/nb189Share';
export default {
  name: 'finsh',
  components: {clock},
  data() {
    return{
      score: 0, //结果
      remark: {
        text: ''
      }, //评级
      timeCost: 0, //用时
      userInfo: this.$store.state.userInfo,
      tData: this.$store.state.tData
    }
  },
  watch: {
    '$store.state.tData': function(v) {
      this.tData = v;
      this.$store.commit('Timing', this.tData.behavior.clock.outSet); //初始化计时器
    }
  },
  methods: {
    setPoint: function(event, fn) { //根据事件执行埋点
      let setData = [];
      this.tData.finsh.btn.forEach((v, i) => {
        if(v.event === event)
          setData = this.$util.setPoint(v.point.set);
          if(v.point.enforce === true) {
            if(setData.length === 3) {
              _uxt.push(['_trackEvent', setData[0], setData[1], setData[2]]); 
            } else {
              _uxt.push(['_trackEvent', `${this.tData.title}_${channel}_结束页`, '点击按钮', fn]); 
            }
          }
      });
    },
    again: function() { //再测一次
      this.setPoint('again', '再测一次');
      this.$store.commit('Timing', this.tData.behavior.clock.outSet); //防止路由跳转的时候时间没有改变
      this.$store.commit('scoring', 0);
      this.$router.push('/behavior');
    },
    analysis: function() { //查看解析
      this.setPoint('analysis', '查看解析');
      this.$router.push('/analysis');
    },
    more: function() { //了解更多
      console.log(decodeURI(encodeURI(JSON.stringify(this.tData))))
      return;
      this.setPoint('more', '了解更多');
      window.location.href = 'https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU1NDkxODIyNw==&scene=126&bizpsid=0#wechat_redirect';
    },
    domain: function(fnName) { //方便动态调用函数
      switch(fnName) {
        case 'again': return this.again();break;
        case 'analysis': return this.analysis();break;
        case 'more': return this.more();break;
      }
    },
    createImg:function(dom) {
      let that = this,
        width = dom.offsetWidth, //获取dom 宽度
        height = dom.offsetHeight, //获取dom 高度
        canvas = document.createElement("canvas"), //创建一个canvas节点
        scale = 2; //定义任意放大倍数 支持小数
      canvas.width = width * scale; //定义canvas 宽度 * 缩放  
      canvas.height = height * scale; //定义canvas高度 *缩放
      canvas.getContext("2d").scale(scale, scale); //获取context,设置scale 
      let opts = {
        scale: scale, // 添加的scale 参数
        canvas: canvas, //自定义 canvas
        logging: false, //日志开关，便于查看html2canvas的内部执行流程
        width: width, //dom 原始宽度
        height: height,
        useCORS: true // 【重要】开启跨域配置
      };
      html2canvas(dom, opts).then(function (canvas) {
        let context = canvas.getContext('2d');
        // 【重要】关闭抗锯齿
        context.mozImageSmoothingEnabled = false;
        context.webkitImageSmoothingEnabled = false;
        context.msImageSmoothingEnabled = false;
        context.imageSmoothingEnabled = false;
        // 【重要】默认转化的格式为png,也可设置为其他格式
        let img = canvas2image.convertToPNG(canvas, canvas.width, canvas.height);
        img.classList.add('end-img');
        img.style.width = canvas.width / scale + 'px';
        img.style.height = canvas.height / scale + 'px';
        $i('#screenshot')[0].appendChild(img);
      });
    },
    screenShot: function() { //操作dom完成截图操作
      let dom = $i('#app')[0].cloneNode(true),
        container = $i('#domContianer')[0],
        firstChild = container.firstChild,
        headshot = $i('#headshot')[0],
        footshot = $i('#footshot')[0];
      if(firstChild) { //如果有重复加载页面，避免容器插入过多相同的节点
        container.removeChild(firstChild);
      }
      container.appendChild(dom);
      setTimeout(() => {
        this.checkHeight(dom);
        this.createImg(container);
      }, 500);
      let  head = $i('#finshHead')[1], 
        tip = $i('#finshTip')[1], 
        btn = $i('#finshBtn')[1];
      head.style.display = 'none'; //隐藏不需要的元素
      tip.style.display = 'none';
      btn.style.display = 'none';
      head.parentNode.insertBefore(headshot, head.nextSibling);
      tip.parentNode.insertBefore(footshot, tip.nextSibling);
    },
    calc: function() { //计分
      let fa = ``;
      if(this.tData.behavior.clock.order === 1) { //计算用时，为1时计算正计时
        this.timeCost = this.$store.state.Timing;
      } else { //计算倒计时
        this.timeCost = this.tData.behavior.clock.outSet - this.$store.state.Timing;
      }
      if(this.$store.state.Timing == this.tData.behavior.clock.outSet) { //防止浏览器前进后退数据丢失,将数据用本地存储保存起来
        let userData = JSON.parse(sessionStorage.getItem('parentingUser'));
        if(!userData) return;
        this.$store.commit('Timing', userData.Timing);
        this.score = userData.score;
      } else {
        if(this.tData.finsh.container.formula) { //如果自带计算公式
          fa = JSON.stringify(this.tData.finsh.container.formula.replace(/\$time/g, this.timeCost).replace(/\$score/g, this.$store.state.scoring));
          this.score = this.$util.evil(fa);
        } else { //默认计算公式
          this.score = (this.$store.state.Timing * 0.4 + this.$store.state.scoring * 8).toFixed(0);
        }
        sessionStorage.setItem('parentingUser', `{"Timing":${this.$store.state.Timing}, "score":${this.score}}`);
      }
      this.tData.finsh.container.remark.data.forEach((v, i) => { //根据评分得到评语以及配图
        if(this.score >= v.interval.min && this.score < v.interval.max){
          this.remark = v;
        }
      });
      // if(this.userInfo.headimgurl) {
      if(this.tData.finsh.container.avatar.img.url) {
          let img = new Image();
          img.style.cssText = `
            max-width: 1rem;
            max-height: 1rem;
            margin-top: -.05rem;
            border: .05rem solid #0ba29a;
            border-radius: 50%;
            `;
          // img.src = this.userInfo.headimgurl;
          // img.src = this.getBase64ByUrl(this.userInfo.headimgurl).then(v=> { //解决头像跨域问题
          img.src = this.getBase64ByUrl(this.tData.finsh.container.avatar.img.url).then(v=> {
            img.src = v;
            img.onload = () => {
              this.$refs['avatar-container'].appendChild(img);
              this.screenShot();
            }
          })
        } else {
          this.screenShot();
        }
    },
    getBase64ByUrl: (src, callback, outputFormat) => { //根据路径将图片下载下来转成base64
      var xhr = new XMLHttpRequest();
      xhr.open('GET', src, true);
      xhr.responseType = 'arraybuffer';
      return new Promise((resolve, reject) => {
        xhr.onload = function(e) {
          if (xhr.status == 200) {
            var uInt8Array = new Uint8Array(xhr.response);
            var i = uInt8Array.length;
            var binaryString = new Array(i);
            while (i--) {
              binaryString[i] = String.fromCharCode(uInt8Array[i]);
            }
            var data = binaryString.join('');
            var base64 = window.btoa(data);
            var dataUrl = "data:" + (outputFormat || "image/png") + ";base64," + base64;
            resolve(dataUrl);
          }
        };
        xhr.send();
      });
    },
    checkHeight(dom) { //确认dom是否需要增加高度
      dom.parentNode.style.height = 'initial';
      dom.parentNode.style.overflow = 'initial';
      dom.style.height = 'initial';
      dom.style.overflow = 'initial';
      dom.firstChild.style.height = 'initial';
      if(document.documentElement.clientHeight < dom.offsetHeight) {
        dom.style.height = '120vh';
      } else {
        dom.style.height = '100vh';
        dom.firstChild.style.height = '100vh';
      }
    }

  },
  created: function() {
    this.calc();
    if(this.tData.finsh.bg) {
      this.$store.commit('setAppBg', this.tData.finsh.bg.url);
      this.$store.commit('setAppBgStyle', this.tData.finsh.bg.style);
    }
    document.title = this.tData.finsh.title || this.tData.title;
  },
  mounted: function() {
    let share = this.tData.finsh.share;
    if(share) {
      share.title = share.title.replace(/\$title/g, this.remark.animal).replace(/\$time/g, this.timeCost).replace(/\$score/g, this.$store.state.scoring);
      share.desc = share.desc.replace(/\$title/g, this.remark.animal).replace(/\$time/g, this.timeCost).replace(/\$score/g, this.$store.state.scoring);
    }
    Share.initWeixin(share);
    if(this.tData.finsh.point.enforce != false) {
      const setData = this.$util.setPoint(this.tData.finsh.point.set);
      if(setData.length === 3) {
        _uxt.push(['_trackEvent', setData[0], setData[1], setData[2]]); 
      } else {
        _uxt.push(['_trackEvent', `${this.tData.title}_${channel}_结束页`, '进入页面', '访问结束页']); 
      }
    }
  }
}
</script>

<style lang="scss" scoped>
$img: '../../public';

