  display: flex;
  align-items: center;
  justify-content: center;
  span {
    font-family: FZLTZH;
    font-size:.26rem;
  }
  .finsh-score {
    font-size: .24rem;
  }
  .finsh-time {
    margin-right: .4rem;
    font-family: FZLTDH;
    font-size: .3rem;
  }
}

.finsh-screenshot {
  margin-top: .26rem;
  padding-left: .8rem;
}

